<template>
    <div class="monaco-container">
        <div ref="container" class="monaco-editor"></div>
    </div>
</template>
<script>
import * as monaco from 'monaco-editor';
export default {
    props: {
        // 
        codes: {
            type: String,
            default: function () {
                return '';
            }
        },
        readOnly: {
            type: Boolean,
            default: function () {
                return true;
            }
        },
        // 
        editorOptions: {
            type: Object,
            default: function () {
                return {
                    selectOnLineNumbers: true,
                    roundedSelection: false,
                    readOnly: false, // 
                    cursorStyle: 'line', // 
                    automaticLayout: false, // 
                    glyphMargin: true, // 
                    useTabStops: false,
                    fontSize: 30, // 
                    autoIndent: false // 
                };
            }
        }
    },
    data() {
        return {
            value: ''
        };
    },

    mounted() {
        this.monacoEditor = monaco.editor.create(this.$refs.container, {
            value: this.codes, // props
            language: 'javascript',
            theme: 'vs-dark', // vs, hc-black, or vs-dark
            folding: true, // 
            foldingHighlight: true, // 
            foldingStrategy: 'indentation', //   auto | indentation
            showFoldingControls: 'always', //  always | mouseover
            disableLayerHinting: true, // 
            emptySelectionClipboard: false, // 
            selectionClipboard: false, // 
            codeLens: false, // 
            scrollBeyondLastLine: false, // 
            colorDecorators: true, // 
            accessibilitySupport: 'off', //   "auto" | "off" | "on"
            lineNumbers: 'on', //   "on" | "off" | "relative" | "interval" | function
            lineNumbersMinChars: 5, //    number
            enableSplitViewResizing: false,
            readOnly: this.readOnly,
            minimap: {
                enabled: false // 
            }
        });
    }
};
</script>
<style lang="scss" scoped>
.monaco-editor {
    height: 300px;
    width: 100%;
    text-align: left;
}
.monaco-container {
    height: 400px;
}
</style>
